import searchBar from '@/components/uni-search/search-bar'
import productCategory from '@/components/product-category/productCategory'
import interfaces from '@/utils/interfaces.js'
export default {
	components: {
		searchBar,
		productCategory,
	},
	data() {
		return {
			searchVal: '', //搜索关键字
			categoryList: [], //产品类目
			backgroundColor: "#F8F8F8", //修改类目背景色
			type: "",
			cate_id: "",
			needid:""
		  // modifyParams:""//修改参数
		}
	},
	onLoad(option) {
		// this.modifyParams=JSON.parse(option.params);
		this.needid=option.id;
		this.cate_id=option.cate_id;
		console.log(this.cate_id)
		this.initData();
	},
	methods: {
		initData() {
			//修改当前的发布信息
			//debugger
			
			this.request({
				url: interfaces.getCategroyData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						id: '',
					}
				},
				success: (res) => {
					this.categoryList = res.data;

				}
			})
		},
		search(res) {
			this.searchVal = res.value
		},
		updateValue(item) {
			this.cate_id=item.id;
		},
		stepBtn() { //下一步
			if (!this.cate_id == '') {
				uni.navigateTo({
					url: '/pages/personalCenter/modify/PublishNext?id='+this.needid+'&cate_id='+this.cate_id
				});
				//console.log(this.modifyParams);
			} else {
				uni.showToast({
					title: '请选择品类',
					icon: "none"
				});
			}
		},

	}
}
